.backdrop {
  height: 100%;
  width: 100%;
  background: rgba(46, 47, 66, .4);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.popup-content {
  text-align: center;
  background-color: #fff;
  border-radius: 5px;
  padding: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.close-popup {
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
}

.modal-btn {
  width: 340px;
  height: 60px;
  color: #fff;
  cursor: pointer;
  background-color: #1d9fdb;
  border: none;
  border-radius: 3px;
  padding: 10px 20px;
  font-size: large;
  font-weight: bold;
  transition-property: background-color, transform;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.modal-btn:hover {
  background-color: #45a049;
  transform: scale(1.03);
}

.modal-text {
  width: 320px;
}

.modal-img {
  height: 120px;
  width: 120px;
}

